.gallery {
	width: 90vw;
	max-width: 1240px;
	height: 415px;
	position: relative;
}

.gallery__view {
	height: 100%;
	width: 100%;
	object-fit: cover;
	border-radius: 25px;
}

.gallery__previous {
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	left: 24px;
	z-index: 1;
	cursor: pointer;
}

.gallery__next {
	position: absolute;
	top: 50%;
	transform: translate(0, -50%) rotate(180deg);
	right: 24px;
	z-index: 1;
	cursor: pointer;
}

.gallery__counter {
	position: absolute;
	bottom: 24px;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 1;
	color: #fff;
	font-weight: 500;
}

/*Responsive*/

@media screen and (max-width: 768px) {
	.gallery__previous, 
	.gallery__next {
		width: 32px;
	}
}

@media screen and (max-width: 576px) {
	.gallery {
		height: 255px;
	}
	.gallery__previous,
	.gallery__next {
		width: 12px;
	}
	.gallery__counter {
		display: none;
	}
}